.menu-header {
	background-color: white;
	display: flex;
	justify-content: space-between;
	flex-direction: row;
	margin-top: 10px;
	border-radius: 30px;
	width: 42%;
	flex-wrap: wrap;
}

.menu-header .nav-link {
	color: black;
	border-radius: 28px;
	width: auto;
	font-size: 18px;
	text-align: center;
	font-weight: 600;
}

.menu-header .selected-menu {
	background: #ff0606;
	color: white;
}

.slide-enter {
	transform: translateX(100%);
	transition: transform 1000ms ease-out;
}

.slide-enter-active {
	transform: translateX(0%);
}

.slide-exit {
	width: 100%;
	transform: translateX(0%);
}

.slide-exit-active {
	transform: translateX(-100%);
}

@media only screen and (max-width: 768px) {
	.menu-header {
		width: 80%;
		display: flex;
		justify-content: space-between;
		align-items: center;
		border-radius: 21px;
		margin-top: 0;
	}

	.menu-header .nav-link {
		color: black;
		border-radius: 30px;
		font-size: 14px;
		font-weight: 500;
	}

	.menu-header .selected-menu {
		background: #ff0606;
		color: white;
	}

	.menu-wrapper {
		padding-left: 0%;
		padding-right: 0%;
	}
}

